"use client"

import DistributeRewardsDrawer from "@/app/components/drawers/DistributeRewardsDrawer"
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  Chip,
  TextField,
  Button,
} from "@mui/material"
import { useState } from "react"

type RewardRow = {
  id: number
  name: string
  email: string
  level: number
  date: string
  rewardAmount: string
  status: "Success" | "Failed"
}

const Page = () => {
  const [search, setSearch] = useState("")

  const columns = [
    { id: "name", label: "User" },
    { id: "email", label: "Email" },
    { id: "level", label: "Level" },
    { id: "date", label: "Date" },
    { id: "rewardAmount", label: "Reward Amount" },
    { id: "status", label: "Status" },
  ] as const

  const data: RewardRow[] = [
    {
      id: 1,
      name: "John Bushmill",
      email: "Johnb@mail.com",
      level: 2,
      date: "1 min ago",
      rewardAmount: "$1000",
      status: "Failed",
    },
    {
      id: 2,
      name: "Jane Doe",
      email: "jane@mail.com",
      level: 3,
      date: "5 min ago",
      rewardAmount: "$500",
      status: "Success",
    },
    {
      id: 3,
      name: "Alice Smith",
      email: "alice@mail.com",
      level: 1,
      date: "10 min ago",
      rewardAmount: "$200",
      status: "Success",
    },
  ]

  const statusBgColors: Record<string, string> = {
    Success: "#4CAF50",
    Failed: "#F44336",
  }

  const statusColors: Record<string, string> = {
    Success: "#fff",
    Failed: "#fff",
  }

  const filteredData = data.filter((row) =>
    row.name.toLowerCase().includes(search.toLowerCase())
  )

  const [open, setOpen] = useState(false)

  return (
    <div className="p-4 bg-[#1f1f1f] min-h-screen text-white">
      <div className="flex justify-between items-center mb-4">
        <TextField
          label="Search user"
          variant="outlined"
          size="small"
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          sx={{
            label: { color: "#aaa" },
            input: { color: "#eee" },
            "& .MuiOutlinedInput-root": {
              "& fieldset": { borderColor: "#555" },
              "&:hover fieldset": { borderColor: "#888" },
              "&.Mui-focused fieldset": { borderColor: "#A78BFA" },
            },
          }}
        />
        <Button variant="contained" sx={{ fontWeight: 600 }} onClick={() => setOpen(true)}>
          Distribute Awards Manually
        </Button>
      </div>

      <div className="font-semibold text-xl my-3">Rewards Distribution</div>

      <TableContainer component={Paper} sx={{ background: "#2b2b2b" }}>
        <Table>
          <TableHead>
            <TableRow>
              {columns.map((col) => (
                <TableCell
                  key={col.id}
                  sx={{
                    color: "#eee",
                    borderBottom: "1px solid #444",
                    fontWeight: 600,
                  }}
                >
                  {col.label}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {filteredData.map((row) => (
              <TableRow key={row.id}>
                {columns.map((col) => (
                  <TableCell key={col.id} sx={{ color: "#ccc" }}>
                    {col.id === "status" ? (
                      <Chip
                        label={row[col.id as keyof RewardRow] as string}
                        sx={{
                          backgroundColor:
                            statusBgColors[row.status] || "#888",
                          color: statusColors[row.status] || "#fff",
                          fontWeight: 500,
                        }}
                      />
                    ) : (
                      row[col.id as keyof RewardRow]
                    )}
                  </TableCell>
                ))}
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <DistributeRewardsDrawer open={open} onClose={() => setOpen(false)} />
    </div>
  )
}

export default Page
